<template>
  <div class="wrapper">
    <div class="fireplace">
       <transition name="slide-fade">
        <img @click="fadeGost" src="/img/ashgost.png" alt="gost" class="ashgost" v-if="show">
       </transition>
       <img @click="rollSkull" src="/img/skull.png" alt="pic"  ref="skull" class="skull">
    </div>
    <button class="next" v-show="flag == 2" @click="next">Дальше</button>
  </div>
</template>
<script>
export default {
   data() {
     return {
       show:true,
       flag:0,
       timer: ''
     }
   },
   methods: {
     fadeGost () {
       this.show = false;
       this.flag += 1;
     },
     rollSkull () {
       let x = 200; 
       let skull = this.$refs.skull;
       skull.classList.add('roll')
       this.timer = setInterval(() => {
         skull.style.left = x + 'px';
         x = x - 20;
         if (x == -100) {
           this.flag += 1;
         }
       },100);
       
     },
     next () {
        window.clearInterval(this.timer);
        this.$router.push('/level3')
     }
   }
}
</script>
<style scoped>
  @font-face {
    font-family: 'horror';
    src: url('/fonts/ofont.ru_ds_eraser2-webfont.woff2') format('woff2'),
        url('/fonts/ofont.ru_ds_eraser2-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;

    }
  .wrapper {
    width: 600px;
    margin: 50px auto;
  }
 
  .fireplace {
    width:100%;
    height: 400px;
    background-image:url('/img/fireplace.jpg');
    background-repeat:no-repeat;
    background-size:100% 100%;
    position: relative;
    overflow: hidden;
  }
  .ashgost {
    width: 100px;
    height: 200px;
    position: absolute;
    top:30px;
    right:30px;
    cursor:pointer;
  }
  .skull {
    width:60px;
    height:70px;
    position:absolute;
    bottom:20px;
    left:200px;
    cursor:pointer;
    transition: all 2s;
  }
  .roll {
    animation: spin 4s linear 0s infinite;
  }
  @keyframes spin {
    from {transform:rotate(0deg);}
    to {transform:rotate(-360deg);}
  }
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
  }
  .next {
    font-family:horror;
    color:red;
    display: block;
    margin: 20px auto;
    font-size:25px;
    width: 120px;
    text-align: center;
    cursor:pointer;
  }
</style>


